@media only screen and (min-width: 780px) {
  .AddGoal-Form {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 20px;
  }
  .AddGoal-Form .light-form-heading {
    color: #424874;
    margin: 0;
    margin-bottom: 20px;
  }
  .AddGoal-Form .dark-form-heading {
    color: white;
    margin: 0;
    margin-bottom: 20px;
  }
  .AddGoal-Form .light-form-input {
    position: relative;
    width: 90%;
    height: 30px;
    outline: 2px solid #424874;
    border: none;
    font-weight: 600;
    font-size: 15px;
    padding: 4px 10px;
    font-family: "Raleway", sans-serif;
  }
  .AddGoal-Form .dark-form-input {
    position: relative;
    width: 90%;
    height: 30px;
    outline: 2px solid #424874;
    border: none;
    font-weight: 600;
    font-size: 15px;
    padding: 4px 10px;
    font-family: "Raleway", sans-serif;
  }
  .AddGoal-Form .light-form-input:focus {
    /* background-color: #f4eeff; */
    color: #424874;
    font-weight: 600;
    font-size: 15px;
    letter-spacing: 1px;
    outline: 2px solid #424874;
    transition: 0.1s ease-in-out;
    font-family: "Raleway", sans-serif;
  }
  .AddGoal-Form .dark-form-input:focus {
    /* background-color: #f4eeff; */
    color: black;
    font-weight: 600;
    font-size: 15px;
    letter-spacing: 1px;
    outline: 2px solid #424874;
    transition: 0.1s ease-in-out;
    font-family: "Raleway", sans-serif;
  }
  .AddGoal-Form .light-form-btn {
    position: relative;
    width: 20vh;
    height: auto;
    background-color: #424874;
    border: none;
    letter-spacing: 1px;
    padding: 10px 10px;
    margin-top: 20px;
    font-family: "Raleway", sans-serif;
    color: white;
  }
  .AddGoal-Form .dark-form-btn {
    position: relative;
    width: 20vh;
    height: auto;
    background-color: #393e46;
    border: none;
    letter-spacing: 1px;
    padding: 10px 10px;
    margin-top: 20px;
    font-family: "Raleway", sans-serif;
    color: white;
  }
  .AddGoal-Form .light-form-btn:hover {
    cursor: pointer;
  }
  .AddGoal-Form .dark-form-btn:hover {
    cursor: pointer;
  }
}
